<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card class="box-card">
      <!-- 添加角色按钮 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="ShowAddRoles">添加角色</el-button>
        </el-col>
      </el-row>
      <!-- 角色列表i区 -->
      <el-table :data="rolesList">
        <!-- 每一列添加展开列 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              v-for="(preventItem,index) in scope.row.children"
              :key="index"
              :class="['border-bottom',index===0?'border-top':'','vcenter']"
            >
              <!-- 一级权限 -->
              <el-col :span="5">
                <el-tag
                  closable
                  @close="removeRolesId(scope.row,preventItem.id)"
                >{{preventItem.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 二级权限和三级权限 -->
              <el-col :span="19">
                <!-- 通过for循环 渲染二级权限 -->
                <el-row
                  v-for="(childItem,index) in preventItem.children"
                  :key="index"
                  :class="[index===0?'':'border-top','vcenter']"
                >
                  <!-- 二级权限渲染 -->
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRolesId(scope.row,childItem.id)"
                    >{{childItem.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- 三级权限渲染 -->
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="(grandsonItem,index) in childItem.children"
                      :key="index"
                      closable
                      @close="removeRolesId(scope.row,grandsonItem.id)"
                    >{{grandsonItem.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!-- 索引列 -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="roleName" label="角色名称"></el-table-column>
        <el-table-column prop="roleDesc" label="角色描述"></el-table-column>
        <el-table-column label="操作" width="290px">
          <template slot-scope="scope">
            <!-- {{ scope.row }}delete -->
            <el-button type="primary" size="mini" icon="el-icon-edit" @click="ShowEditRoles(scope.row.id)">编辑</el-button>
            <el-button type="danger" size="mini" icon="el-icon-delete" @click="removeRolesInfo(scope.row.id)">删除</el-button>
            <el-button
              type="warning"
              size="mini"
              icon="el-icon-setting"
              @click="setRightsDialog(scope.row)"
            >分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 添加角色对话框 -->
    <el-dialog title="添加新角色" :visible.sync="addDialogVisible" width="50%" @close="addDialogClose">
      <!-- 角色信息权限验证 -->
      <el-form
        :model="addFormRoles"
        :rules="addFormRolesrules"
        ref="addFormRolesRef"
        label-width="100px"
      >
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="addFormRoles.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="addFormRoles.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRoles">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑修改角色对话框 -->
    <el-dialog title="修改角色" 
    :visible.sync="editDialogVisible" 
    width="50%"
    @close="editDialogClose">
      <!-- 角色信息权限验证 -->
      <el-form
        :model="editFormRoles"
        :rules="editFormRolesrules"
        ref="editFormRolesRef"
        label-width="100px"
      >
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="editFormRoles.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="editFormRoles.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editRoles">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 分配权限对话框 -->
    <el-dialog
      title="分配权限"
      :visible.sync="RightsDialogVisible"
      width="40%"
      @close="rightsDialogClose"
    >
      <!-- 树形结构展示 -->
      <el-tree
        :data="rightsList"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="Defkeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="RightsDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRights">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 所有角色数据
      rolesList: [],
      RightsDialogVisible: false, //控制分配权限对话框显示/隐藏
      rightsList: [], //所有权限数据
      //属性控件的属性绑定对象
      treeProps: {
        label: "authName",
        children: "children",
      },
      Defkeys: [], //默认选中节点id值的数组
      rolesId: "",
      addDialogVisible: false, //控制添加角色对话框显示/隐藏
      //添加角色对象
      addFormRoles: {
        roleName: "",
        roleDesc: "",
      },
    //   添加角色对象规则
      addFormRolesrules: {
        roleName: [
          { required: true, message: "请输入角色名称", trigger: "blur" },
          { min: 2, max: 8, trigger: "blur" },
        ],
        roleDesc: [
          { required: true, message: "请输入角色描述", trigger: "blur" },
          { min: 3, trigger: "blur" },
        ],
      },
      editDialogVisible:false, //控制角色编辑修改对话框显示/隐藏
      editFormRoles:{}, 
    //   编辑修改对话框验证规则
      editFormRolesrules:{
           roleName: [
          { required: true, message: "请输入角色名称", trigger: "blur" },
          { min: 2, max: 8, trigger: "blur" },
        ],
        roleDesc: [
          { required: true, message: "请输入角色描述", trigger: "blur" },
          { min: 3, trigger: "blur" },
        ],
      }
    };
  },
  methods: {
    // 获取角色列表数据的接口
    async AjaxRoleslist() {
      let { data: response } = await this.$http.get("roles");
      // console.log(response.data)
      if (response.meta.status !== 200)
        return this.$message.error("获取角色列表失败");
      this.rolesList = response.data; //没有返回则获取角色列表数据成功，挂载到data中
    },
    //根据id删除对应的权限
    async removeRolesId(roles, rightsId) {
      let confirmResult = await this.$confirm(
        "此操作将永久删除该权限, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((error) => error);
      if (confirmResult !== "confirm") return this.$message.info("已取消删除");
      let { data: response } = await this.$http.delete(
        `roles/${roles}/rights/${rightsId}`
      );
      if (response.meta.status !== 200)
        return this.$message.error("权限删除失败");
      roles.children = response.data;
    },
    //点击触发分配权限列表对话框
    async setRightsDialog(roles) {
      this.rolesId = roles.id;
      // 在弹出分配权限对话框的同时获取权限列表数据
      let { data: response } = await this.$http.get("rights/tree");
      if (response.meta.status !== 200)
        return this.$message.error("获取权限数据失败");
      //  console.log(response.data)
      // 获取的权限数据保存到data中
      this.rightsList = response.data;
      // 递归获取三级节点的id
      this.getLeafKeys(roles, this.Defkeys);
      this.RightsDialogVisible = true;
    },
    // 通过递归的形式获取角色下所有三级权限的id，并保存到Defdeys数组中
    getLeafKeys(node, arr) {
      // 如果当前node节点不包含children属性，则是三级节点
      if (!node.children) {
        return arr.push(node.id);
      }
      node.children.forEach((element) => {
        this.getLeafKeys(element, arr);
      });
    },
    //监听分配权限对话框关闭事件
    rightsDialogClose() {
      this.Defkeys = [];
    },
    //点击确定按钮分配权限
    async allotRights() {
      let keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys(),
      ];
      let keysString = keys.join(",");
      let { data: response } = await this.$http.post(
        `roles/${this.rolesId}/rights`,
        {
          rids: keysString,
        }
      );
      if (response.meta.status !== 200)
        return this.$message.error("分配权限失败");
      this.$message.success("分配权限成功");
      this.RightsDialogVisible = false;
      this.rolesList();
    },
    // 点击添加角色按钮，添加角色
    ShowAddRoles() {
      this.addDialogVisible = true;
    },
    //监听添加角色对话框关闭事件
    addDialogClose() {
      // 重置对话框
      this.$refs.addFormRolesRef.resetFields();
    },
    //点击添加角色按钮，进行预校验
    addRoles() {
      this.$refs.addFormRolesRef.validate(async (valid) => {
        if (!valid) {
          return this.$message.info("角色预校验失败");
        }
        let { data: response } = await this.$http.post("roles", {
          roleName: this.addFormRoles.roleName,
          roleDesc: this.addFormRoles.roleDesc,
        });
        if (response.meta.status != 201) {
          return this.$message.error("添加角色失败");
        }
        this.$message.success("添加角色成功");
        this.addDialogVisible = false;
        this.AjaxRoleslist();
      });
    },
    // 点击编辑按钮，展示编辑修改角色对话框
   async ShowEditRoles(rolesId){
        let {data:response} = await this.$http.get('roles/'+rolesId)
        if(response.meta.status !=200) return this.$message.error('查询角色id失败');
        this.editFormRoles=response.data
        this.editDialogVisible=true
    },
    // 点击确定按钮，对编辑对话框提交修改
    editRoles(roleId){
        this.$refs.editFormRolesRef.validate(async valid=>{
            if(!valid) return this.$message.info('预校验角色失败');
        let {data:response} = await this.$http.put(`roles/${this.editFormRoles.roleId}`,{
            roleName:this.editFormRoles.roleName,
            roleDesc:this.editFormRoles.roleDesc
        })
            if(response.meta.status !=200){
                return this.$message.error('修改角色失败')
            }
            // 修改成功，关闭对话框
            this.editDialogVisible=false
            //提示消息
            this.$message.success('修改角色成功')
            //刷新数据
            this.AjaxRoleslist();
        })
    },
    //监听编辑对话框关闭事件
    editDialogClose(){
        this.$refs.editFormRolesRef.resetFields()
    },
    // 删除角色信息
   async removeRolesInfo(rolesId){
    let confirmResoult=await this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(error=>error)
       if(confirmResoult !== "confirm"){
           return this.$message.info('已取消删除')
       }
     let {data:response} = await this.$http.delete("roles/"+rolesId)
     if(response.meta.status !=200) return this.$message.error('删除角色失败');
       this.$message.success('删除角色成功');
       this.AjaxRoleslist();

    }
  },
  created() {
    //创建角色列表
    this.AjaxRoleslist();
  },
};
</script>
<style lang="less" scoped>
.el-tag {
  margin: 10px;
}
.border-bottom {
  border-bottom: 1px solid #eee;
}
.border-top {
  border-top: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>