<template>
    <div>
            <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>权限管理</el-breadcrumb-item>
    <el-breadcrumb-item>权限列表</el-breadcrumb-item>
    </el-breadcrumb> 
    <!-- 卡片视图 -->
    <el-card class="box-card">
        <!-- 用户权限列表表格 -->
        <el-table :data="rightsList" border stripe>
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column prop="authName" label="权限名称"></el-table-column>
          <el-table-column prop="path" label="路径"></el-table-column>
          <el-table-column prop="level" label="权限等级">
              <template slot-scope="scope">
                  <el-tag v-if="scope.row.level==='0'">一级</el-tag>
                  <el-tag type="success" v-else-if="scope.row.level==='1'">二级</el-tag>
                  <el-tag type="warning" v-else>三权</el-tag>
              </template>
          </el-table-column>
        </el-table>
    </el-card>
    </div>

</template>
<script>
export default {
    data() {
        return {
           rightsList:[] //权限列表数据
        }
    },
    methods: {
        // 获取权限列表数据接口
       async  AjaxRightsList(){
           let {data:response} = await this.$http.get("rights/list")
        //    console.log(response.data)
           if(response.meta.status !==200) return this.$message.error("获取权限列表数据失败")
           this.rightsList=response.data //如果没有返回时，获取列表数据成功，挂载列表数据
       }
    },
    created(){
        //获取所有的权限
        this.AjaxRightsList()
    }
}
</script>
<style lang="less" scoped>

</style>