<template>
    <el-container class="el-container">
    <!-- 头部部分 -->
  <el-header>
    <div>
      <img src="../assets/heima.png" alt="">
      <span>电商后台管理系统</span>
    </div>
    <el-button type="info" @click="Logout">退出</el-button>
    </el-header>
    <!-- 主体部分 -->
  <el-container>
    <!-- 左侧部分 -->
    <el-aside :width="isCollapse?'64px':'200px'">
      <div class="toggle-botton" @click="toggleCollapse">|||</div>
      <!-- 侧边栏菜单区 -->
      <el-menu
      background-color="#333744"
      text-color="#fff"
      active-text-color="#409eff"
      unique-opened
      :collapse="isCollapse"
      :collapse-transition="false"
      router
      :default-active="activePath">
      <!-- 一级菜单 -->
      <el-submenu :index="item.id+''" v-for="item in menulist" :key="item.id">
        <!-- 一级菜单模板区域 -->
        <template slot="title">
          <!-- 图标 -->
          <i :class="iconlist[item.id]"></i>
          <!-- 文本 -->
          <span style="marginLeft:10px">{{item.authName}}</span>
        </template>
        <!-- 二级菜单 -->
        <el-menu-item :index="'/'+item.path" v-for="item in item.children" :key="item.id"
        @click="saveNavState('/'+item.path)">
          <!-- 图标 -->
            <i class="el-icon-menu"></i>
            <!-- 文本 -->
          <span>{{item.authName}}</span>
        </el-menu-item>
      </el-submenu>
    </el-menu>
    </el-aside>
    <!--右侧部分 -->
    <el-main>
      <router-view></router-view>
    </el-main>
  </el-container>
</el-container>
</template>

<script>
export default {
  data() {
    return {
      menulist:[],   //左侧菜单所有数据
      iconlist:{
        125:"iconfont icon-users",
        103:"iconfont icon-tijikongjian",
        101:"iconfont icon-shangpin",
        102:"iconfont icon-danju",
        145:"iconfont icon-baobiao",
      },
      isCollapse:false,
      activePath:""
    }
  },
  // 在左侧列表刚一加载时，立即获取左侧菜单数据
  created(){
    this.getMenuList()
    this.activePath=window.sessionStorage.getItem("activePath")
  },
  methods: {
    // 点击退出按钮,先清空token,在重定向到登录页面
    Logout(){
      window.sessionStorage.clear();
      this.$router.push("/login")
    },
    // 左侧菜单数据接口获取
    async getMenuList(){
     let {data:response} = await this.$http.get("menus")
    //  判断数据里面的状态码是否等于200   若不等于则返回msg获取失败
     if(response.meta.status!=200) return this.$message.error(response.meta.msg);
      // 若没有返回则是获取成功
      this.menulist=response.data
    //  console.log(response)
    },
    // 点击按钮，控制左侧菜单收放
    toggleCollapse(){
      this.isCollapse = !this.isCollapse
    },
    // 保存连接的激活状态
    saveNavState(activePath){
      window.sessionStorage.setItem("activePath",activePath)
    }
  },
  
}
</script>
<style scoped lang="less">
.el-container
{
  height: 100%;
}
  .el-header
  {
    background: #373d41;
    display: flex;
    justify-content: space-between;
    padding-left: 0;
    align-items: center;
    color: #fff;
    font-size: 23px;
    >div{
      display: flex;
      align-items: center;
    }
    span{
      margin-left: 15px;
    }
  }
  .el-aside
  {
    background: #333744;
    .el-menu{
      border-right: 0;
    }
  }
  .el-main
  {
    background: #eaedf1;
  }
  .toggle-botton
  {
    background: #4a5064;
    font-size: 12px;
    line-height: 24px;
    color: #fff;
    text-align: center;
    letter-spacing: 0.3em;
    cursor: pointer;
  }
</style>
