<template>
    <div>
         <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card class="box-card">
        <!-- 添加分类按钮 -->
         <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCate">添加商品</el-button>
        </el-col>
      </el-row>
      <!--商品分类表格 -->
       <zk-table :data="cateList" :columns="columns"
       :expand-type="false"
       :selection-type="false"
       show-index
       index-text="#"
       border
       :show-row-hover="false"
       >
       <!-- 是否有效 -->
       <template slot="valid" slot-scope="scope">
        <i class="el-icon-success" style="color:lightseagreen" v-if="scope.row.cat_deleted==false"></i>
        <i class="el-icon-error" v-else></i>
    </template>
    <!-- 排序 -->
      <template slot="level" slot-scope="scope">
       <el-tag v-if="scope.row.cat_level===0" size="mini">一级</el-tag>
       <el-tag type="success" size="mini" v-else-if="scope.row.cat_level===1">二级</el-tag>
       <el-tag type="warning" size="mini" v-else>三级</el-tag>
    </template>
    <!--  操作-->
     <template slot="handle" slot-scope="scope">
          <el-button type="primary" size="mini" icon="el-icon-edit" @click="editCateInfo(scope.row.cat_id)">编辑</el-button>
            <el-button type="danger" size="mini" icon="el-icon-delete" @click="removeCateInfo(scope.row.cat_id)">删除</el-button>
    </template>
    </zk-table>
    <!-- 分页 -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-sizes="[1, 5, 10, 15]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-card>
    <!-- 点击添加商品对话框 -->
    <el-dialog
  title="添加分类"
  :visible.sync="addCateDialogVisible"
  width="50%"
  @close="addFormCateClose"
  >
  <!-- 验证 -->
  <el-form :model="addFormCate" :rules="addFormCateRules" ref="addFormCateRef" label-width="100px">
  <el-form-item label="分类名称" prop="cat_name">
       <el-input v-model="addFormCate.cat_name"></el-input>
  </el-form-item>
  <el-form-item label="父级分类">
      <!-- options 指定数据源 -->
        <el-cascader 
    :options="parentCateList"
    :props="cascaderProps"
    v-model="parentCateKeys"
    @change="parentHandleChange"
    clearable>
    </el-cascader>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="addCateDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addCateInfo">确 定</el-button>
  </span>
</el-dialog>
<!-- 点击编辑展示对话框 -->
<el-dialog
  title="修改分类"
  :visible.sync="editCateDialog"
  width="50%"
  @close="editCateFromClose">
  <el-form :model="editFormCate" :rules="editFormCateRules" ref="editFormCateRef" label-width="100px">
  <el-form-item label="分类名称" prop="cat_name">
    <el-input v-model="editFormCate.cat_name"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editCateDialog = false">取 消</el-button>
    <el-button type="primary" @click="editCateEnsure">确 定</el-button>
  </span>
</el-dialog>
    </div>
</template>
<script>
export default {
    data() {
        return {
        // 商品查询条件
         queryInfo:{
             type:"",
             pagenum:1,
             pagesize:1,
         },
         cateList:[], //分类数据列表
         total:0, //总数居条数
         //为table指定列的定义
         columns:[
            {
                label: '分类名称',
                prop:'cat_name',
            },
            {
                label:'是否有效',
                type:'template', //定义当前列定义为模板列
                template:'valid' //当前模板列的名称
            },
            {
                label:'排序',
                type:'template', //定义当前列定义为模板列
                template:'level' //当前模板列的名称
            },
            {
                label:'操作',
                type:'template', //定义当前列定义为模板列
                template:'handle',//当前模板列的名称
            }
         ],
         addCateDialogVisible:false, //控制分类添加对话框的显示/隐藏
         editCateDialog:false, //控制修改对话框的显示/隐藏
         addFormCate:{
             cat_name:'', //分类名称
             cat_pid:0, //分类父 ID
             cat_level:0, //分类当前层级
         },
        //  验证规则
         addFormCateRules:{
             cat_name:[
                 { required: true, message: '请输入分类名称', trigger: 'blur' }, 
             ]
         },
         parentCateList:[] , //父级分类列表数据
         //指定级联选择器配置对象
         cascaderProps:{
             value:'cat_id',
             label:'cat_name',
             children:'children',
             expandTrigger: 'hover'
         },
         parentCateKeys:[], //选中的父级分类
         editFormCate:{},
         editFormCateRules:{
             cat_name:[
                 { required: true, message: '请输入分类名称', trigger: 'blur' }, 
             ]
         }
        }
    },
    methods: {
        // 获取商品分类列表i数据接口
       async AjaxCategories(){
           let {data:response} = await this.$http.get("categories",{
               params:this.queryInfo
           })
        //    console.log(response)
        if(response.meta.status !=200) return this.$message.error('获取商品列表失败');
        this.cateList=response.data.result
        this.total=response.data.total
       },
    // 监听pagesize改变
    handleSizeChange(newPageSize){
        this.queryInfo.pagesize=newPageSize
        this.AjaxCategories()
    },
    // 监听pagenum改变
    handleCurrentChange(newPageNum){
        this.queryInfo.pagenum=newPageNum
        this.AjaxCategories()
    },
    //点击添加按钮展示添加商品对话框
    showAddCate(){
        //先获取父级分类数据的列表，再展示
        this.AjaxParentCateList()
        this.addCateDialogVisible=true
    },
    //点击对话框确定按钮，添加商品分类
    addCateInfo(){
    this.$refs.addFormCateRef.validate(async valid=>{
        if(!valid) return //没有返回则调取添加分类接口
     let {data:response} = await this.$http.post('categories',this.addFormCate)
      if(response.meta.status !==201){
          return this.$message.error('添加分类失败')
      }
      this.addCateDialogVisible=false
      this.$message.success('添加分类成功')
      this.AjaxCategories()
    })
    },
    //获取父级分类数据列表的接口
    async AjaxParentCateList(){
        let {data:response} = await this.$http.get("categories",{
            params:{
                type:2
            }
        })
        if(response.meta.status !==200){
            return this.$message.error("获取数据失败")
        }
        this.parentCateList=response.data
        console.log(response.data)
    },
    //选择项发生改变时触发的事件
    parentHandleChange(){
        console.log(this.parentCateKeys)
    },
    //监听添加分类对话框关闭事件
    addFormCateClose(){
     this.$refs.addFormCateRef.resetFields()
     this.parentCateKeys=[]
      this.parentCateList.cat_level=0
      this.parentCateList.cat_pid=0
    },
    // 点击编辑，展示对话框
    async editCateInfo(catId){
     let {data:response} = await this.$http.get("categories/"+catId)
     if(response.meta.status !=200){
         return this.$message.error('查询分类失败')
     }
       this.editFormCate=response.data
        this.editCateDialog=true
    },
    //点击编辑对话框确定按钮，编辑分类成功
    editCateEnsure(){
        this.$refs.editFormCateRef.validate(async valid=>{
            if(!valid) return
       let {data:response} = await this.$http.put(`categories/${this.editFormCate.cat_id}`,{
           cat_name:this.editFormCate.cat_name
       })
            if(response.meta.status != 200){
                return this.$message.error('编辑分类失败')
            }
            this.editCateDialog=false
            this.$message.success('编辑分类成功')
            this.AjaxCategories()
        })
    },
    //监听编辑对话框的关闭事件
    editCateFromClose(){
        this.$refs.editFormCateRef.resetFields()
    },
    // 点击删除，展示删除对话框
   async removeCateInfo(catID){
     let confirmCateResult= await this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(error=>error)
        // console.log(confirmCateResult)
        if(confirmCateResult !=="confirm"){
            return this.$message.info("删除分类失败")
        }
        let {data:response} = await this.$http.delete("categories/"+catID)
        console.log(response.data)
        if(response.meta.status !== 200){
            return this.$message.error('删除分类失败！')
        }
        this.$message.success('删除分类成功！')
        this.AjaxCategories()
    }   
    },
    created(){  
     this.AjaxCategories()
    }
}
</script>
<style lang="less" scoped>
.el-cascader{
    width: 100%;
}
</style>