<template>
    <div>
        <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>用户管理</el-breadcrumb-item>
    <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card class="box-card">
  <el-row :gutter="20">
  <el-col :span="8">
    <el-input placeholder="请输入内容" class="input-with-select" v-model="queryInfo.query" clearable
    @clear="getUserList">
    <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
  </el-input>
  </el-col>
  <el-col :span="4">
      <el-button type="primary" @click="addDialogVisible=true">添加用户</el-button>
  </el-col>
</el-row>
<!-- 用户列表表格 -->
<el-table :data="userlist" border stripe>
    <el-table-column type="index" label="#"></el-table-column>
    <el-table-column prop="username" label="姓名"></el-table-column>
    <el-table-column prop="email" label="邮箱"></el-table-column>
    <el-table-column prop="mobile" label="电话"></el-table-column>
    <el-table-column prop="role_name" label="角色"></el-table-column>
    <el-table-column prop="mg_state" label="状态">
       <template slot-scope="scope">
           <el-switch v-model="scope.row.mg_state" @change="userStateChange(scope.row)"></el-switch>
       </template>
    </el-table-column>
    <el-table-column label="操作" width="190px">
        <template slot-scope="scope">
    <el-tooltip  effect="dark" content="编辑信息" placement="top">
        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
    </el-tooltip>
     <el-tooltip  effect="dark" content="删除用户" placement="top">
        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUser(scope.row.id)"></el-button>
    </el-tooltip>
     <el-tooltip  effect="dark" content="分配角色" placement="top">
        <el-button type="warning" icon="el-icon-setting" size="mini" @click="AssignRoles(scope.row)"></el-button>
    </el-tooltip>
        </template>
    </el-table-column>
     </el-table>
     <!-- 分页 -->
     <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-card>
    <!-- 对话框弹出和隐藏 -->
    <el-dialog
  title="添加用户"
  :visible.sync="addDialogVisible"
  width="50%"
  @close="addDialogClosed">
  <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
  <el-form-item label="用户名" prop="username">
    <el-input v-model="addForm.username"></el-input>
  </el-form-item>
  <el-form-item label="密码" prop="password">
    <el-input v-model="addForm.password" type="password"></el-input>
  </el-form-item>
  <el-form-item label="邮箱" prop="email">
    <el-input v-model="addForm.email"></el-input>
  </el-form-item>
  <el-form-item label="电话" prop="mobile">
    <el-input v-model="addForm.mobile"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addDialogUser">确 定</el-button>
  </span>
</el-dialog>
<!-- 编辑用户对话框 -->
<el-dialog
  title="编辑用户"
  :visible.sync="editDialogVisible"
  width="40%"
  @close="editDialogClose">
  <!-- 编辑用户对话框数据列表 -->
  <el-form ref="editFormRef" :model="editForm" label-width="70px" :rules="addFormRules">
    <!-- 用户名 -->
  <el-form-item label="用户名">
    <el-input v-model="editForm.username" disabled></el-input>
  </el-form-item>
  <!-- 邮箱 -->
  <el-form-item label="邮箱" prop="email">
    <el-input v-model="editForm.email"></el-input>
  </el-form-item>
  <!-- 电话 -->
  <el-form-item label="电话" prop="mobile">
    <el-input v-model="editForm.mobile"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editUserInfo">确 定</el-button>
  </span>
</el-dialog>
<!-- 分配角色对话框 -->
 <el-dialog
  title="分配角色"
  :visible.sync="RolseDialogVisible"
  width="40%"
  @close="RolseDialogClose">
  <div>当前的用户：{{rolesInfo.username}}</div>
  <div>当前的角色：{{rolesInfo.role_name}}</div>
  <div>分配新角色： <el-select v-model="RolesValue" placeholder="请选择">
    <el-option
      v-for="item in rolesList"
      :key="item.id"
      :label="item.roleName"
      :value="item.id">
    </el-option>
  </el-select></div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="RolseDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="seveRolesInfo">确 定</el-button>
  </span>
</el-dialog>
    </div>
</template>
<script>
export default {
    data() {
        // 验证邮箱的表达式
          var checkEmail=(rule,value,cb)=>{
              const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
           if(regEmail.test(value)){
               return cb()
           }
           cb(new Error('邮箱格式不正确'))
          }
          //验证手机号的表达式
           var checkMobile=(rule,value,cb)=>{
              const regMobile = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
           if(regMobile.test(value)){
               return cb()
           }
           cb(new Error('请输入正确的手机号'))
          }
        return {
           queryInfo:{
               query:"",
               pagenum:1,
               pagesize:2
           },
           userlist:[] , //用户列表数据
           total:0,
           addDialogVisible:false,
           //添加用户表单数据
           addForm:{
               username:'',
               password:'',
               email:'',
               mobile:''
           },
        //    验证规则对象
           addFormRules:{
               username:[
               { required: true, message: '请输入用户名称', trigger: 'blur' },
             { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
               ],
                password:[
               { required: true, message: '请输入密码', trigger: 'blur' },
             { min: 6, max: 15, message: '长度在 6到 15 个字符', trigger: 'blur' }
               ],
               email:[
                   { required: true, message: '请输入邮箱', trigger: 'blur' },
                   {validator:checkEmail,trigger:'blur'}
               ],
               mobile:[
                   { required: true, message: '请输入电话号码', trigger: 'blur' },
                   {validator:checkMobile,trigger:'blur'}
               ],
            },
            //控制编辑用户对话框的显示/隐藏，默认false
            editDialogVisible:false,
            editForm:[], //用户查询修改数据存储
            RolseDialogVisible:false, //控制分配角色对话框显示/隐藏
            rolesInfo:{}, //被分配的角色信息
            rolesList:[], //分配角色的数据
            RolesValue:'' //绑定选中的角色
        }
    },
    created(){
        this.getUserList()
    },
    methods: {
        // 用户列表数据接口获取
        async getUserList(){
           let {data:response}= await this.$http.get("users",{params:this.queryInfo})
           if(response.meta.status !=200) return this.$message.error("获取用户列表失败");
           this.userlist=response.data.users
           this.total=response.data.total
           console.log(response)
        },
        // 监听pagesize改变的事件
        handleSizeChange(pagesize){
            this.queryInfo.pagesize=pagesize
             this.getUserList()
        },
        // 监听页码值改变的事件
        handleCurrentChange(pagenum){
        this.queryInfo.pagenum=pagenum
        this.getUserList()
        },
        //监听switch开关的变化
       async userStateChange(userinfo){
            console.log(userinfo)
          let {data:response}=await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
          if(response.meta.status !=200) {
              userinfo.mg_state = !userinfo.mg_state
              return this.$message.error('更新用户状态失败')
          }
          this.$message.success('更新用户状态成功')
        },
        //监听添加用户对话框关闭事件
        addDialogClosed(){
         this.$refs.addFormRef.resetFields()
        },
        //点击按钮，添加新用户
         addDialogUser(){
            this.$refs.addFormRef.validate(async valid=>{
              console.log(valid)
              if(!valid) return
            //   预校验通过，发起添加用户的网络请求
            let {data:response} = await this.$http.post("users",this.addForm)
              if(response.meta.status !=201){
                  this.$message.error('添加用户失败')
              }
              this.$message.success('添加用户成功')
              this.addDialogVisible=false
              this.getUserList()
            })
        },
        //展示修改用户对话框
       async showEditDialog(userid){
          console.log(userid)
         let  {data:response} = await this.$http.get('users/'+userid)
         if(response.meta.status !=200) return this.$message.error('查询修改用户失败');
          this.editForm=response.data
          this.editDialogVisible=true
        },
        //点击编辑对话框取消按钮重置事件
        editDialogClose(){
          this.$refs.editFormRef.resetFields()
        },
        //点击确定对编辑对话框内的数据进行预验证
        editUserInfo(){
          // 用户验证规则
          this.$refs.editFormRef.validate(async valid=>{
            if(!valid) return this.$message.error("用户验证失败")
           let {data:response} = await this.$http.put('users/'+this.editForm.id,{
             email:this.editForm.email,
             mobile:this.editForm.mobile
           })
           if(response.meta.status != 200){
             return this.$message.error('修改用户失败')
           }
            //更新用户成功
            this.$message.success('修改用户成功')
            //修改成功后关闭对话框
           this.editDialogVisible=false
           //重新刷新数据
           this.getUserList()
          })
        },
        //删除用户信息
       async removeUser(userid){
          // console.log(userid)
        let confirmResult= await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(error => error)
        // console.log(confirmResult)
        if(confirmResult != "confirm"){
          return this.$message.info("已取消删除")
        }
         let {data:response} = await this.$http.delete('users/'+userid)
         console.log(response)
         if(response.meta.status !==200){
           return this.$message.error('删除用户失败')
         }
         this.$message.success('删除用户成功！')
         //删除成功后刷新用户列表数据
         this.getUserList()
        },
        //展示分配角色的对话框
       async AssignRoles(rolesInfo){
          this.rolesInfo=rolesInfo
        let {data:response} = await this.$http.get('roles')
        if(response.meta.status !==200){
          return this.$message.error('获取分配角色失败')
        }
        this.rolesList=response.data
          this.RolseDialogVisible=true
        },
        //点击分配角色对话框确定按钮，分配新角色
        async seveRolesInfo(){
          if(! this.RolesValue){
            return this.$message.error('请选择分配角色')
          }
        let {data:response} = await this.$http.put(`users/${this.rolesInfo.id}/role`,{
          rid:this.RolesValue
        })
        if(response.meta.status !=200){
          return this.$message.error('分配新角色失败')
        }
        this.$message.success('分配新角色成功')
        this.RolseDialogVisible=false
        this.getUserList()
        },
        // 监听分配角色对话框关闭事件
        RolseDialogClose(){
         this.RolesValue=''
         this.rolesInfo={}
        }
    },

}
</script>
<style lang="less" scoped>

</style>