<template>
    <div class="login_container">
        <div class="login_box">
            <!-- 头像部分 -->
            <div class="avatar_box">
                <img src="../assets/logo.png" alt="">  
            </div>
            <!-- 登录部分 -->
            <el-form class="login_form" :model="loginForm" ref="loginFormRef">
            <!-- 账号部分 -->
            <el-form-item>
            <el-input prefix-icon="el-icon-s-custom" v-model="loginForm.username"></el-input>
            </el-form-item>
            <!-- 密码部分 -->
             <el-form-item>
            <el-input prefix-icon="el-icon-s-goods" v-model="loginForm.password" type="password"></el-input>
            </el-form-item>
            <!-- 登录按钮部分 -->
             <el-form-item class="btns">    
              <el-button type="primary" @click="login">登录</el-button>
              <el-button type="info" @click="resetLoginForm">重置</el-button>
            </el-form-item>
            </el-form>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            loginForm:{
                username:"admin",
                password:"123456"
            }
        }
    },
    methods: {
        // 点击重置按钮,重置from表单
        resetLoginForm(){
            // console.log(this)
            this.$refs.loginFormRef.resetFields();
        },
        // 点击登录按钮,实现登录预验证
        login(){
            this.$refs.loginFormRef.validate(async valid=>{
                // console.log(valid)
                if(!valid) return;
                let {data:response}= await this.$http.post("login",this.loginForm)
                // console.log(response)
                if(response.meta.status != 200) return this.$message.error("登录失败");;
                this.$message.success('登录成功')
                console.log(response)
                window.sessionStorage.setItem("token",response.data.token)
                // 通过编程式导航,实现登录成功跳转到后台主页面
                this.$router.push({
                    path:"/home"
                })
            })
        }
    },
}
</script>
<style lang="less" scoped>
.login_container
{
    background: #2b4b6b;
    height: 100%;
}
.login_box
{
    width: 450px;
    height: 300px;
    border-radius: 3px;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.avatar_box{
    height: 130px;
    width: 130px;
    // border: 1px solid gray;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    background: #fff;
    img{
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: #eee;
    }
}
.btns
{
    display: flex;
    justify-content: flex-end;
}
.login_form
{
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
}
</style>